#header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0; }
  #header button {
    outline: none;
    border: none;
    background-color: transparent;
    padding: 0; }
  #header .main-bnt {
    display: none;
    background-color: #fff;
    color: #E96B67;
    margin-bottom: 40px; }
    @media (max-width: 1024px) {
      #header .main-bnt {
        display: inline; } }

.header__wrap {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 50;
  border-bottom: 2px solid #D0312B; }

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 0;
  min-height: 120px; }
  @media (max-width: 479px) {
    .header__content {
      min-height: 70px; } }

/* //////////////////////////first block header */
.header__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%; }

.header__logo {
  /* Только если меньше контейнера */
  margin-right: 20px; }
  @media (max-width: 1300px) {
    .header__logo {
      margin-right: calc(10px + 10 * ((100vw - 320px) / 980)); } }

.logo__wrap {
  position: relative;
  z-index: 5;
  display: block; }
  .logo__wrap img {
    width: 104px;
    height: 42px; }
    @media (max-width: 479px) {
      .logo__wrap img {
        width: 80px;
        height: 30px; } }

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media (max-width: 1024px) {
    .menu {
      position: relative;
      z-index: -1;
      position: fixed;
      width: 100%;
      height: 100%;
      overflow: auto;
      top: -100%;
      left: 0;
      background-color: #E96B67;
      padding: 100px 15px 30px 15px;
      transition: all .5s ease 0s;
      display: flex;
      flex-direction: column;
      justify-content: flex-start; }
      .menu::before {
        content: '';
        position: fixed;
        width: 100%;
        height: 100px;
        top: 0;
        left: 0;
        transition: all .3s ease 0s;
        z-index: 2; } }

@media (max-width: 1024px) {
  .menu_active {
    top: 0; }
    .menu_active::before {
      background-color: #E96B67; } }

@media (max-width: 1024px) {
  .menu__wrap {
    margin-bottom: 30px; } }

.menu__list {
  padding: 0;
  margin: 0;
  list-style: none; }
  @media (min-width: 1025px) {
    .menu__list {
      display: flex;
      justify-content: space-evenly; } }
  @media (max-width: 1024px) {
    .menu__list {
      display: flex;
      flex-direction: column;
      align-items: center; } }

.menu__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* Только если меньше контейнера */
  margin-right: 40px;
  padding: 5px 0;
  position: relative; }
  @media (max-width: 1300px) {
    .menu__item {
      margin-right: calc(20px + 20 * ((100vw - 320px) / 980)); } }
  .menu__item:last-child {
    margin-right: 0;
    margin-left: 40px; }
    .menu__item:last-child::before {
      content: '';
      position: absolute;
      left: -40px;
      top: 0;
      width: 1px;
      height: 100%;
      background-color: #000; }
  @media (max-width: 1024px) {
    .menu__item {
      padding: 0;
      margin: 0;
      margin-bottom: 30px; }
      .menu__item:last-child {
        margin: 0;
        margin-bottom: 100px; }
        .menu__item:last-child::before {
          left: 50%;
          transform: translate(-50%);
          top: 200px;
          width: 150%;
          height: 1px;
          background-color: #fff; } }
  @media (max-width: 768px) {
    .menu__item {
      margin-bottom: 25px; }
      .menu__item:last-child {
        margin: 0;
        margin-bottom: 26px; }
        .menu__item:last-child::before {
          left: 50%;
          transform: translate(-50%);
          top: 80px;
          width: 150%;
          height: 1px;
          background-color: #fff; } }

.menu__link {
  transition: color .3s ease 0s;
  font-size: 20px; }
  @media (min-width: 1025px) {
    .menu__link:hover {
      color: #E96B67; } }
  @media (max-width: 1024px) {
    .menu__link {
      color: #fff;
      font-weight: 800;
      font-size: 80px;
      line-height: 130%; } }
  @media (max-width: 768px) {
    .menu__link {
      font-size: 45px; } }

.mob-social {
  display: none; }
  @media (max-width: 1024px) {
    .mob-social {
      display: flex;
      justify-content: center; } }

.mob-social__link {
  margin: 0 15px; }
  .mob-social__link i {
    font-size: 36px;
    color: #fff; }

/* ---------------burger */
.burger {
  display: none;
  position: relative;
  width: 25px;
  height: 15px; }
  @media (max-width: 1024px) {
    .burger {
      display: block; } }
  .burger span {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #E96B67;
    top: 50%;
    left: 0;
    border-radius: 10px;
    transition: all .3s ease 0s; }
    .burger span:first-child {
      top: 0; }
    .burger span:last-child {
      top: 100%; }

.burger_active span {
  opacity: 0;
  background-color: #fff; }
  .burger_active span:first-child {
    opacity: 1;
    transform: rotate(-45deg);
    top: 6px; }
  .burger_active span:last-child {
    opacity: 1;
    transform: rotate(45deg);
    top: 6px; }

/* __________________________________dark-menu_________________________ */
@media (min-width: 1025px) {
  .dark-menu #header .menu__item:last-child::before {
    background-color: #fff; }
  .dark-menu #header .menu__link {
    color: #fff; }
    .dark-menu #header .menu__link:hover {
      color: #E96B67; } }

